@import url('https://fonts.googleapis.com/css2?family=Arima+Madurai:wght@800&family=Barlow:wght@300&family=Maven+Pro&family=Monda:wght@700&family=Poppins:wght@300&display=swap');

.home-head{
  color: white;
  padding-top: 6rem;
  background-color: #303030;
  height: 100vmin;
  display: flex;
  align-items: center;
  justify-content: space-around;
  padding-left: 20px;
  padding-right: 20px;
  /* font-size: 2rem; */
}

.home-content{
  padding-right: 40px;
}
.home-content h1{
  font-size: calc( 30px + 1.5vw);
  font-family: 'Monda', sans-serif;
  opacity: 0;
  animation: slideBottom 1s ease forwards;
}
.home-content h3{
  font-size: calc(20px + 1.5vw);
  font-family: 'Maven Pro', sans-serif;
  color: #c2fbd7;
  opacity: 0;
  animation: slideBottom 1s ease forwards;
}

.home-content h3 span {
  font-family: 'Maven Pro', sans-serif;
}
.home-content button{
  background-color: transparent;
  margin-top: 2rem;
  width: 8rem;
  height: 3rem;
  color: white;
  border: 1px solid white;
  border-radius: 0.5rem;
  font-size: 1rem;
  font-weight: 600;
  transition: 0.3s;
  opacity: 0;
  animation: slideBottom 1s ease forwards;
}
.home-content button:hover{
  background-color: #90e1ae;
  border: 1px solid #90e1ae;
  color: black;
  /* box-shadow: 1px 1px 1px 0.2rem rgb(202, 227, 214) ; */
  
}
.home-img{
  width: calc(330px + 12vw);
  
}

.home-img img{
  width: 100%;
  pointer-events: none;
  opacity: 0;
  animation: scaleUp 1.5s ease forwards;
}

.home-data{
  background-color: #303030;
  height: 70vh;
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: space-evenly;
}
.home-data img{
  pointer-events: none;
  width: 100%;
  opacity: 0;
  animation: slideRight 2s ease forwards;
}
.code-test-img{
  width: calc(300px + 6vw);
}

.card{
  visibility: visible;
  background-color: rgb(35, 35, 35);
  height: 100%;
  width: 100%;
  padding: 1rem;
  display: flex;
  /* border-radius: 0.5rem; */
  flex-direction: column;
  align-items: center;
  justify-content: space-evenly;
  width: 100vw;
  /* box-shadow: 10px 10px 5px 5px black; */
}
.card-head{
  color: rgb(224, 139, 107);
  padding: 1rem;
  font-size: calc(17px + 1vw);
  font-weight: 600;
  text-align: center;
}
.card-body{
  text-align: center;
  color: whitesmoke;
  font-size: calc(12px + 0.7vw);
  /* font-size: 1.1rem; */
}



/* Keyframe Animations */
@keyframes slideBottom {
  0% {
    transform: translateY(50px);
    opacity: 0;
  }
  
  100% {
    transform: translateY(0);
    opacity: 1;
  }
}


@keyframes scaleUp {
  0% {
    transform: scale(0.5);
    opacity: 0;
  }
  
  100% {
    transform: scale(1);
    opacity: 1;
  }
}

@keyframes slideRight {
  0% {
    transform: translateX(-100px);
    opacity: 0;
  }
  
  100% {
    transform: translateX(0);
    opacity: 1;
  }
}


/* Media query */
@media only screen and (max-width:750px)  {
  .home-head{
    flex-direction: column;
    height: 100vmax;
  }
    
}